<template>
  <div class="login">
    <!-- 卡片 -->
    <el-card class="box-card">
      <h1 style="margin: 0 0 14px 90px">代码分析工具</h1>
      <!-- 登录 or 注册 -->
      <el-radio-group v-model="labelPosition" class="radioGroup" size="small">
        <el-radio label="login">登录</el-radio>
        <el-radio label="signIn">注册</el-radio>
      </el-radio-group>

      <!-- user输入表单 -->
      <el-form label-position="right" label-width="80px" :model="user">
        <el-form-item label="用户名" prop="name" :rules="[{ required: true, message: '请输入用户名', trigger: 'blur' }]">
          <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" :rules="[{ required: true, message: '请输入密码', trigger: 'blur' }]">
          <el-input type="password" v-model="user.password" show-password></el-input>
        </el-form-item>
        <el-form-item v-if="labelPosition === 'signIn'" label="确认密码" prop="checkPassword"
          :rules="[{ required: true, message: '请输入再次输入密码', trigger: 'blur' }]">
          <el-input type="password" v-model="user.checkPassword" show-password></el-input>
        </el-form-item>
        <!--按钮-->
        <el-form-item class="button">
          <el-button v-if="labelPosition === 'login'" type="warning" @click="login"
            :disabled="user.name === '' || user.password === ''" round>登录
          </el-button>
          <el-button v-if="labelPosition === 'signIn'" type="warning" @click="login"
            :disabled="user.name === '' || user.password === '' || user.checkPassword === ''" round>注册
          </el-button>
          <el-button @click="resetForm" round>重置</el-button>
          
          
          <!-- <div style="float: right;" >
            <router-link class="link-type" :to="'/register'">立即注册</router-link>
          </div> -->
        </el-form-item>
     

      </el-form>
    </el-card>
  </div>
</template>
 
<script>
export default {
  data() {
    return {
      labelPosition: 'login',  // 开始先定位到登录
      // 用户数据
      user: {
        name: '',
        password: '',
        checkPassword: '',
      }
    }
  },
  methods: {
    // 登录
    login() {
      this.$router.push('project')
    },
    // 注册
    signIn() {
      if (this.user.checkPassword !== this.user.password) {
        this.$message.error("两次输入的密码不一致!")
      }
    },
    // 重置表单
    resetForm() {
      this.user.name = ""
      this.user.password = ""
      this.user.checkPassword = ""
    }
  }
}
</script>
 
<style>
.login {
  width: 100%;
  height: 100%;
  /*position: fixed;*/
  /*background-image: url(~@/assets/img/login.png);*/
  /*background-repeat: no-repeat;*/
  /*background-size: cover;*/
}

.box-card {
  width: 480px;
  border: 1px solid #1f808c;
  position: fixed;
  top: 50%;
  left: 50%;
  /* margin: 200px 0 0 530px; */
  transform: translate(-50%, -50%)
}

.radioGroup {
  margin: 0 0 10px 150px;
}

.button {
  margin: 0 0 0 50px;
}
</style>