// ����Ӧ�ó����·����
import Vue from 'vue'
import VueRouter from 'vue-router'
// ��ʱ�Ϳ�����Vueʵ��������·������
Vue.use(VueRouter)

// �������
import Index from '../views/index'
import Login from '../views/login'

// ��������¶һ��·����
export default new VueRouter({
  mode:'history',
  routes: [
    {
      path: '/', redirect: '/login',
    },
    {
      path: '/login', component: Login
    },
    {
      path: '/code',
      component: Index,
      redirect: '/home',
      // ��·��
      children: [
        {
          path: '/home',
          name: 'home',
          component: () => import(('@/components/homePage/index'))
        },
        {
          path: '/project',
          name: 'project',
          component: () => import(('@/components/projectManage/project'))
        },
        {
          path: '/problem',
          name: 'problem',
          component: () => import(('@/components/problem/index'))
        },
        {
          path: '/analyze',
          name: 'analyze',
          component: () => import(('@/components/analyze/index'))
        },
        {
          path: '/tool',
          name: 'tool',
          component: () => import(('@/components/tool/index'))
        },
        {
          path: '/report',
          name: 'report',
          component: () => import(('@/components/report/index'))
        },
      ]
    },

  ]
})